@import url(https://fonts.googleapis.com/css?family=Playfair+Display:400,700|Roboto:300,400&display=swap);
.menuContainer {
    height: 100%;
    width: 100%;
}

.blueThemedFont{
    font-weight: 500;
    color: #4EA2BB;
}

.scrollContainer {
    height: 100vh;
    width: 100%;
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    overflow-y: scroll;
    -webkit-scroll-snap-type: y mandatory;
        -ms-scroll-snap-type: y mandatory;
            scroll-snap-type: y mandatory;
}

.scrollFullPage {
    height: 100vh;
    width: 100vw;
    scroll-snap-align: center;
    display: flex;
}

.listItemBuffer {
    height: calc(50vh - 2rem);
}

.textBoxObserver {
    position: fixed;
    top: 30vh;
    height: 40vh;
    width: 100vw;
}

.listScrollContainer {
    /* display: flex; */
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    height: 100%;
    left: 0;
    top: 0;
    width: 26vw;
    padding: 0 5vw;
    box-sizing: border-box;
    list-style: none;
    margin: 0;
    text-align: center;
    overflow: visible;
}

.listScrollContainer::-webkit-scrollbar {
    display: none;
}

.listIndexContainer {
    height: 128px;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}

.listItemContainer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    width: 100%;
    scroll-snap-align: center;
    height: 100vh;
    right: 0;
}

.indexCharContainer {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 40px;
    flex-direction: column;
    position: relative;
    cursor: pointer;
}

.indexChar {
    font-size: 2rem;
    font-weight: lighter;
    font-family: "Times new roman", Times, serif;
}

.yearIndex {
    position: absolute;
    bottom: -0.45rem;
    font-size: 0.5rem;
    -webkit-transition: 0.2s ease-out;
    transition: 0.2s ease-out;
}

.contentsContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    cursor: pointer;
    opacity: 0;
    -webkit-transition: 0.5s opacity ease;
    transition: 0.5s opacity ease;
    padding: 25px;
}

.visible {
    opacity: 1;
}

.arrowComposition {
    height: 50px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    width: 225px;
}

.spine {
    width: 175px;
    height: 4px;
    background: black;
    position: absolute;
    right: 17px;
    top: calc(25px - 2px);
    -webkit-transition: 0.25s ease-out;
    transition: 0.25s ease-out;
}

.mediumArrow {
    height: 40px;
    width: 40px;
}

.circle {
    position: absolute;
    width: 120px;
    height: 120px;
    right: 0;
    -webkit-transform: translate(37.5px, 0);
            transform: translate(37.5px, 0);
}

.outer {
    fill: transparent;
    stroke-width: 4;
    opacity: 0.2;
    /* transition: 1s ease-out; */
}

.year {
    position: fixed;
    bottom: 2rem;
    right: 2.5rem;
    display: flex;
}

.yearStyling {
    font-size: 1.75rem;
    font-family: "Roboto";
    color: var(--grey);
}

.yearSingleDigit-appear {
    opacity: 0;
}

.yearSingleDigit-appear-active {
    opacity: 1;
    -webkit-transition: opacity 400ms;
    transition: opacity 400ms;
}

.yearSingleDigit-enter {
    opacity: 0;
}

.yearSingleDigit-enter-active {
    opacity: 1;
    -webkit-transition: opacity 400ms;
    transition: opacity 400ms;
}

.yearSingleDigit-exit {
    opacity: 1;
}

.yearSingleDigit-exit-active {
    opacity: 0;
    -webkit-transition: opacity 400ms;
    transition: opacity 400ms;
}

.dateText {
    position: absolute;
    text-align: center;
    width: 100%;
    opacity: 0;
    -webkit-transition: opacity 500ms;
    transition: opacity 500ms;
}

.dateTime{
    font-size: 3rem;
    top: 4.5rem;
    font-weight: 700;
    width: 100%;
    position: absolute;
}

.dateTimeAnimation-enter{
    opacity: 0;
}
.dateTimeAnimation-enter-active {
    opacity: 1;
    -webkit-transition: opacity 10000ms;
    transition: opacity 10000ms;
}
.dateTimeAnimation-exit {
    opacity: 1;
}
.dateTimeAnimation-exit-active {
    opacity: 0;
    -webkit-transition: opacity 10000ms;
    transition: opacity 10000ms;
}
/* .dateTimeColor{
    transition: 0.5s ease-out;
} */

@media (min-width: 320px) {
    .outer {
        stroke-dasharray: 537;
        stroke-dashoffset: 0;
    }
    .contentsContainer{
        padding-bottom: 3rem;
    }
}
@media (min-width: 480px) {
    /* smartphones, Android phones, landscape iPhone */
}
@media (min-width: 600px) {
    /* portrait tablets, portrait iPad, e-readers (Nook/Kindle), landscape 800x480 phones (Android) */
}
@media (min-width: 801px) {
    /* tablet, landscape iPad, lo-res laptops ands desktops */
}
@media (min-width: 1025px) {
    .outer {
        -webkit-transition: 0.25s ease-out;
        transition: 0.25s ease-out;
    }
    .contentsContainer:hover .outer {
        opacity: 1;
        /* animation-direction: alternate; */
    }
    .contentsContainer:hover .spine {
        width: 150px;
    }
    .contentsContainer{
        padding-bottom: 0rem;
    }
}
@media (min-width: 1281px) {
    /* hi-res laptops and desktops */
}

:root {
    --bgd-color: #cbe9f4;
    --light: #93d5eb;
    --medium: #66bbd8;
    --dark: #4da2bb;
    --bush: #ffffff;
    --trunk: #c2653c;
    --trunk2: #9d5d5d;
    --cloud: #ffffff;
    --sun: transparent;
    --rabbit: #ffffff;
}

.container {
    position: fixed;
    background-color: #cbe9f4;
    background-color: var(--bgd-color);
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: -1;
    overflow: hidden;
}

.sun {
    background-color: transparent;
    background-color: var(--sun);
    width: 60px;
    height: 60px;
    border-radius: 100%;
    top: -20px;
    left: 180px;
    position: absolute;
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
}

.rainbow {
    visibility: hidden;
    position: absolute;
    width: 600px;
    height: 270px;
    left: 100px;
    bottom: 82px;
    left: 50%;
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
}
.rainbow.animated {
    -webkit-animation: rainbow 14s linear;
    animation: rainbow 14s linear;
    visibility: visible;
    position: absolute;
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
}
.rainbow .rainbow-colour {
    position: absolute;
    border-top-left-radius: 50% 100%;
    border-top-right-radius: 50% 100%;
    border-bottom: 0;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
    transform: translate(-50%, 0);
    -webkit-transform-origin: 0 100%;
    transform-origin: 0 100%;
    bottom: 0;
    border-bottom: 0 !important;
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
}
.rainbow .rainbow-colour:nth-child(1) {
    position: absolute;
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    border: 10px solid #ac01fd;
    height: 100%;
    width: 100%;
}
.rainbow .rainbow-colour:nth-child(2) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #511ce0;
    height: 261px;
    width: 581px;
}
.rainbow .rainbow-colour:nth-child(3) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #1a9fe2;
    height: 252px;
    width: 562px;
}
.rainbow .rainbow-colour:nth-child(4) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #5ed81d;
    height: 243px;
    width: 543px;
}
.rainbow .rainbow-colour:nth-child(5) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #f8f52a;
    height: 234px;
    width: 524px;
}
.rainbow .rainbow-colour:nth-child(6) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #f7a528;
    height: 225px;
    width: 505px;
}
.rainbow .rainbow-colour:nth-child(7) {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    border: 10px solid #f63b2a;
    height: 216px;
    width: 486px;
}

@-webkit-keyframes rainbow {
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.5;
    }
    80% {
        opacity: 0.5;
    }
    100% {
        opacity: 0;
    }
}

@keyframes rainbow {
    0% {
        opacity: 0;
    }
    10% {
        opacity: 0.5;
    }
    80% {
        opacity: 0.5;
    }
    100% {
        opacity: 0;
    }
}
.cloud-group {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    overflow: hidden;
}

.cloud {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    background-color: #ffffff;
    background-color: var(--cloud);
    width: 80px;
    height: 30px;
    border-radius: 50px;
    left: 5%;
    top: 40px;
    -webkit-animation: clouds 10s linear infinite;
    animation: clouds 10s linear infinite;
}

@-webkit-keyframes clouds {
    50% {
        -webkit-transform: translateX(20px);
        transform: translateX(20px);
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}

@keyframes clouds {
    50% {
        -webkit-transform: translateX(20px);
        transform: translateX(20px);
    }
    100% {
        -webkit-transform: translateX(0);
        transform: translateX(0);
    }
}
.weather-container {
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
    position: absolute;
    width: 100%;
    height: 400px;
}


