@-webkit-keyframes drops {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
    5% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translate3d(20px, 200px, 0);
        transform: translate3d(20px, 200px, 0);
        opacity: 0;
    }
}

@keyframes drops {
    0% {
        opacity: 0;
        -webkit-transform: translate3d(0, 0, 0);
        transform: translate3d(0, 0, 0);
    }
    5% {
        opacity: 1;
    }
    80% {
        opacity: 1;
    }
    100% {
        -webkit-transform: translate3d(20px, 200px, 0);
        transform: translate3d(20px, 200px, 0);
        opacity: 0;
    }
}
@-webkit-keyframes snow {
    0% {
        margin-top: 0px;
        opacity: 0;
    }
    5% {
        opacity: 1;
    }
    50% {
        opacity: 1;
        margin-left: 20px;
    }
    100% {
        margin-top: 200px;
        opacity: 0;
    }
}
@keyframes snow {
    0% {
        margin-top: 0px;
        opacity: 0;
    }
    5% {
        opacity: 1;
    }
    50% {
        opacity: 1;
        margin-left: 20px;
    }
    100% {
        margin-top: 200px;
        opacity: 0;
    }
}
