    transform-origin: 50% 50%;
}
.flower.animated {
    position: absolute;
    -webkit-animation: flower 11s ease;
    animation: flower 11s ease;
    visibility: visible;
}
.flower .flower-in {
    position: absolute;
    z-index: 2;
    width: 100%;
    height: 100%;
    border-radius: 100px;
    -webkit-transition: 3s ease background;
    transition: 3s ease background;
