.scale-enter,
.scale-exit {
    position: absolute;
}

@-webkit-keyframes moveFromRight {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@keyframes moveFromRight {
    from {
        -webkit-transform: translateX(100%);
                transform: translateX(100%);
    }
}

@-webkit-keyframes scaleDown {
    to {
        opacity: 0;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}

@keyframes scaleDown {
    to {
        opacity: 0;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
    }
}
.scale-enter-active {
    -webkit-animation: moveFromRight 600ms ease both;
            animation: moveFromRight 600ms ease both;
    z-index: 2;
}

.scale-exit-active {
    -webkit-animation: scaleDown 600ms ease both;
            animation: scaleDown 600ms ease both;
    z-index: 1;
}

